import React from 'react';
import demomixtapetwo from '../res/demo-mixtape-two.JPEG';

import inmyroom from '../res/in-my-room.jpg';
import comedown from '../res/come-down.jpg';

function Projects () {
  return (
    <div>
      <a href="https://soundcloud.com/daviddotflac/in-my-room" title="in my room" target="_blank" rel="noopener noreferrer">
        <img className="photo" src={ inmyroom } alt="in my room" />
      </a>
      <a href="https://soundcloud.com/daviddotflac/come-down" title="come down" target="_blank" rel="noopener noreferrer">
        <img className="photo" src={ comedown } alt="come down" />
      </a>
      <a href="https://soundcloud.com/daviddotflac/sets/demo-mixtape-2" title="demo mixtape 2" target="_blank" rel="noopener noreferrer">
        <img className="photo" src={ demomixtapetwo } alt="demo mixtape 2" />
      </a>
    </div>
  );
}

export default Projects;